input{
    border: 0px;
    border-bottom: 2px solid plum;
    display: flex;
    flex-direction: column;
    margin: 10px;
    align-items: center;
    text-align: center;
    margin: 10px auto;
    margin-top: 30px; 
    width: 170px;
    font-size: 15px;
    outline: none;
}
.title{
    text-align: center;
}
.btn{
    width: 90px;
    background-color: palevioletred;
    border: 0px solid palevioletred;
    border-radius: 5px;
    font-size: 15px;
    padding: 3px;
    display: block;
    margin: 10px auto;
}
.btn:hover{
    cursor: pointer;
    background-color: gray;
    color: white;

}
.values{
    display: flex;
}
li{
    list-style: none;
    margin: 0px 0px;
    border-right: 2px solid black;
    padding: 3px;
    text-align: center;
    width: 80px;
}
.head1{
    font-weight: bold;
    margin: 0px 0px;
    border-bottom: 2px solid black;
    border-top: 2px solid black;
    width: 80px;
    
}
.ans{
    border-bottom: 2px solid black;
    border-top: 2px solid black;
}
.val{
    display: flex;
}
.val ol p{
    width:150px;
}